.container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.emptyCartWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 20px;
  border-bottom: 1px solid #e0e0e0;
}

.paymentContainer {
  display: flex;
  padding-left: 20px;
  align-items: center;
}

.settlementButtonWrapper {
  width: 50%;
  margin-left: auto;
}

.imgContainer {
  display: flex;
}

.img {
  width: 72px;
  height: 72px;
}

.nameContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 16px;
  align-self: stretch;
  width: 40%;
}

.quantityControlWrapper {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.quantityControlItem {
  margin-left: 8px;
}
