.container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.menuWrapper {
  display: flex;
  flex: 1;
  overflow-y: auto;
}

.menuListWrapper {
  width: 6.5em;
  height: 100%;
  background-color: #f5f5f5;
  overflow-y: auto;
}

.menuItemsWrapper {
  height: 100%;
  overflow-y: auto;
  flex-grow: 1;
}

.menuListWrapper::-webkit-scrollbar {
  display: none;
}

.menuListWrapper {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.cardSectionContainer {
  display: flex;
  z-index: 3;
  box-shadow: 0px 0 5px rgba(0, 0, 0, 0.1);
  height: 40px;
}

.cartIconWrapper {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  transform: scale(2);
  background-color: white;
  transform-origin: 0% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 14px;
  box-shadow: 0px -4px 3px rgba(0, 0, 0, 0.1);
}

.priceWrapper {
  display: flex;
  align-self: flex-end;
  margin-left: 32px;
  align-items: center;
}

.buttonWrapper {
  flex: 1;
  margin-left: 32px;
}
